.header {
    height: 400px;
    border-radius: 30px 0 30px 0;
    background-image: linear-gradient(to right, #801041, rgba(70, 6, 81, 1));
    padding: 30px 70px;
    box-sizing: border-box;
    display: flex;
    margin-bottom: 100px;
}

.article {
    width: 2000px;
}

.article>h1 {
    font-size: 50px;
}

.article>h2 {
    font-size: 40px;
    margin: 0 0 30px 0;
}

#movieText {
    color: yellowgreen;
    font-family: 'Sue Ellen Francisco', cursive;
}

.img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

#imgHeader {
    width: 300px;
}

#film {
    background-color: rgba(28, 2, 32, 1);
    padding: 10px 30px;
    color: white;
    display: flex;
    gap: 10px;
    align-items: center;
    border-radius: 50px;
    transition: 0.5s;
}

.btn {
    margin-top: 30px;
    display: flex;
    gap: 20px;
}

#film:hover {
    background-color: #2E3192;
}

@media(max-width: 768px) {
    .header {
        height: 800px;
        padding: 70px 50px;
        display: flex;
        flex-direction: column-reverse;
        margin-bottom: 100px;
    }

    .article {
        width: 100%;
    }

    .article>h1 {
        font-size: 50px;
    }

    .article>h2 {
        font-size: 40px;
        margin: 0 0 30px 0;
    }

    #film {
        background-color: rgba(28, 2, 32, 1);
        padding: 10px 30px;
        color: white;
        display: flex;
        gap: 10px;
        align-items: center;
        border-radius: 50px;
        transition: 0.5s;
    }

    .btn {
        margin-top: 30px;
        display: flex;
        gap: 20px;
    }
}

@media(max-width: 415px) {
    .header {
        height: 700px;
        padding: 80px 20px;
        display: flex;
        flex-direction: column-reverse;
        margin-bottom: 100px;
    }

    .article {
        width: 100%;
    }

    #imgHeader {
        width: 200px;
    }

    .article>h1 {
        font-size: 40px;
    }

    .article>h2 {
        font-size: 30px;
        margin: 0 0 30px 0;
    }

    #film {
        background-color: rgba(28, 2, 32, 1);
        padding: 10px 30px;
        color: white;
        display: flex;
        gap: 10px;
        align-items: center;
        border-radius: 50px;
        transition: 0.5s;
        font-size: 12px;
    }
}